import React from 'react';
import TimeAgo from 'react-timeago';
import f from '../../female.png';
import m from '../../male.png';
import './userprofile.css';
import cross from '../../../cross.png'
const userProfile = ({k, iu,checkFollow,isFollowing,followClick,userFollowing,modalClosed}) => {
let isFollow=false	
return(
<div className='stc'>{iu===undefined?<div className='userprofile shadow-5 ma3 bg-white pa3 ba-black-80 center'></div>:
<div key={k}>
{userFollowing.map((follow,i)=>{
  	 	if(userFollowing[i].following===iu.email){
  	 	isFollow=true;
  	 }
  	 })}
<div className='bg-white ba-black-80 center  h7'>
<div className='w-100 h1 mt2-l'><img src={cross} className='grow z-max fr' height='50px' onClick={()=>{modalClosed()}} width='50px'/></div>
<br/>
<img alt='abc' className='fr mr1 bg-light-green br-100 pa3 ma3  grow shadow-5 w-20-l w-20-m w-30'  src={`https://robohash.org/${iu.id}?200*200`}/>

<div className="black-80 ml4-l ml4-m w-40 "><p className='f3-m f3-l f4  '>{iu.name}</p>
<p className='f3-m f3-l f4  '><span className='b'>Branch</span> {iu.branch}</p>
<p className='f3-m f3-l f4  '><span className='b'>Year</span> {iu.year}</p>
<p className='f3-m f3-l f4  '><span className='b'>Gender</span> {iu.gender==='f'?<img src={f} height='19em' className='pa1 pb0'/>:<img src={m} height='19em' className='pa1 pb0'/>}</p>
<p className='f3-m f3-l f4  '><span className='b'>Followed</span> by {iu.followers}</p>
<p className='f3-m f3-l f4  '><span className='b'>Joined </span><TimeAgo date={iu.joined}  /></p>

</div>
<div key={k+32} className='fr '>{isFollow===false?<button className='w3 h2 bg-blue white' onClick={()=>{followClick(true)}}>Follow</button>:<button className='w4 h2 bg-blue white' onClick={()=>{followClick(false)}}>Unfollow</button>}
</div>
</div>
</div>}
</div>
	)
	}
export default userProfile;


